
export default {
  props: {
    buttonText: {
      type: String,
      required: true,
    },
    buttonIcon: {
      type: String,
      default: null,
    },
    buttonColor: {
      type: String,
      default: null,
    },
    tooltipText: {
      type: String,
      default: null,
    },
    dialogTitle: {
      type: String,
      default: 'Confirm',
    },
    dialogText: {
      type: String,
      default: null,
    },
    icon: {
      type: Boolean,
      default: false,
    },
    confirm: {
      type: Boolean,
      default: true,
    },
    confirmInput: {
      type: String,
      default: null,
    },
    action: {
      type: Function,
      required: true,
    },
  },
  emits: ['click'],
  data() {
    return {
      confirmDialogVisible: false,
      confirmUserInput: '',
      actionInProgress: false,
    }
  },
  methods: {
    async performAction() {
      if (this.actionInProgress) {
        return;
      }

      this.actionInProgress = true;
      try {
        await Promise.resolve(this.action());
        this.confirmDialogVisible = false;
      } catch (error) {
        this.$toast.global.requestError({ error });
      } finally {
        this.actionInProgress = false;
      }
    }
  }
}
